import React, { Component } from 'react';

import { AppContext, Util } from '../../base';
import ApiGroup from '../../components/apigroup';
import ApiContent from '../../components/apicontent';

import styles from './style.module.css';
import { Config, Connection } from '../../configs';
const { isEmpty } = Util;

export default class Apidoc extends Component {
	static contextType = AppContext;

	constructor() {
		super();
		this.state = {
			appName: '',
			apiGroup: [],
			apiDetail: {},
			showInfo: false,
		};
	}

	async componentDidMount() {
		let apiDoc = '';
		if (isEmpty(Config.apiUrl)) {
			Connection.send('/ApiDoc/api', {}).then((result) => {
				apiDoc = result;
				this.setState({ appName: apiDoc.appName, apiGroup: apiDoc.apiGroup });
			});
		} else {
			apiDoc = require('./api.json');
			this.setState({ appName: apiDoc.appName, apiGroup: apiDoc.apiGroup });
		}
	}

	loadApiDetail(api) {
		this.setState({ apiDetail: api });
		this.hideMenu();
	}

	toggleInfo() {
		this.setState({ showInfo: !this.state.showInfo });
	}

	showMenu() {
		let menu = document.getElementById('menu');
		menu.style.left = '0px';
		menu.style.transition = 'all 0.2s ease';

		let menuOverlay = document.getElementById('menuOverlay');
		menuOverlay.style.display = 'block';
	}

	hideMenu() {
		let menu = document.getElementById('menu');
		menu.style.left = '-75%';
		menu.style.transition = 'all 0.2s ease';

		let menuOverlay = document.getElementById('menuOverlay');
		menuOverlay.style.display = 'none';
	}

	render() {
		return !isEmpty(this.state.apiGroup) ? (
			<div className={styles.bodyView}>
				<div
					id='menuOverlay'
					className={styles.menuOverlay}
					onClick={() => this.hideMenu()}></div>
				<div className={styles.menuView} id='menu'>
					<ApiGroup
						apiGroup={this.state.apiGroup}
						getApiDetail={(api) => this.loadApiDetail(api)}
					/>
				</div>
				<div className={styles.detailView}>
					<div className={'alignMiddle ' + styles.title}>
						<label>
							<i
								className={'material-icons ' + styles.menuIcon}
								onClick={() => this.showMenu()}>
								menu
							</i>
							&nbsp;
							{this.state.appName}
						</label>
						<i className={'material-icons'} onClick={() => this.toggleInfo()}>
							info
						</i>
					</div>

					<div
						className={styles.guideView}
						style={{ display: this.state.showInfo ? 'block' : 'none' }}>
						<strong>Usage:</strong>
						<br />
						1. Add new api in <strong>mockdata/api.json</strong>.<br />
						2. Create folder path same as api url in mockdata.
						<br />
						3. Remember to <strong>pull / push</strong> (if have changes).
						<br />
						<br />
						- View this Document on browser.
						<br />
						- Access from browser url / postman.
						<br />
						(example:{' '}
						<a
							href={Config.apiUrl + '/bo/system/ping'}
							target='_blank'
							rel='noopener noreferrer'>
							{Config.apiUrl}/bo/system/ping
						</a>
						)
					</div>

					<ApiContent apiDetail={this.state.apiDetail} />
					<small>Developed by Ha Thanh Tam</small>
					<br />
					<small>Regional FE Team Lead @ Wirecard</small>
				</div>
			</div>
		) : null;
	}
}
