import { HttpService } from '../base';

const Config = {
	// App Info
	httpOption: {
		channel: 'Document',
		appVersion: '1.0.0',
		isMobile: false,
		timeout: 10,
	},

	// Server API
	apiUrl: '',
	baseUrl: process.env.NODE_ENV === 'production' ? '' : 'http://localhost:3002',
};

const Connection = new HttpService(
	Config.baseUrl + Config.apiUrl,
	Config.httpOption,
);

export { Config, Connection };
